
import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react';

import { Button } from 'primereact/button';
import { Card } from 'primereact/card';
import { confirmPopup } from 'primereact/confirmpopup';
import { Dialog } from 'primereact/dialog';
import { FileUpload } from 'primereact/fileupload';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';
import { Link } from "react-router-dom";

import Common from './Common';
import { URL, ellipse, handleChange } from './Constant';

import './css/Template.css';

function TemplateComponent() {

    const role = localStorage.getItem('role');
    const ref = useRef(null);
    const fileRef = useRef(null);

    const [data, setData] = useState([]);
    const [form, setForm] = useState({ id: '', name: '', desc: '' });
    const [showTemplateDialog, setShowTemplateDialog] = useState(false);

    useEffect(() => {
        fetchData();
    }, [])

    const fetchData = () => {
        ref.current?.setLoading(true);
        axios.get(`${URL.BASE}api/template/all`).then((result) => {
            ref.current?.setLoading(false);
            setData(result.data.data);
        }).catch((err) => {
            ref.current?.setLoading(false);
        });
    }

    const header = (g) => {
        return <img src={URL.BASE + 'static/templates/' + g.file_name} alt="Image" className='h-25rem' />;
    }

    const footer = (t) => (
        <div className='h-3rem'>
            {(role == 1) &&
                <Button severity="danger" icon="pi pi-trash" rounded text onClick={(e) => deleteImage(e, t.id)} />
            }
            <Button icon="pi pi-file-edit" rounded text onClick={() => editTemplate(t)} />
        </div>
    )

    const editTemplate = (t) => {
        setForm(t)
        setShowTemplateDialog(true);
    }

    const hideTemplateDialog = () => {
        setForm({ id: '', name: '', desc: '' })
        setShowTemplateDialog(false);
    }

    const deleteImage = (e, id) => {
        e.preventDefault();
        confirmPopup({
            target: e.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: (e) => {
                ref.current?.setLoading(true);
                axios.delete(`${URL.BASE}api/template/delete/${id}`).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true)
                        ref.current?.showToast('success', 'Success', 'Template deleted successfully');
                    else
                        ref.current?.showToast('error', 'Error', 'Error deleting Template');
                    fetchData();
                }).catch((err) => {
                    ref.current?.setLoading(false);
                    ref.current?.showToast('error', 'Error', 'Error deleting Template');
                });
            }
        });
    }

    const updateTemplate = (e) => {
        e.preventDefault();
        ref.current?.setLoading(true);
        axios.put(`${URL.BASE}api/template/update`, form).then((result) => {
            ref.current?.setLoading(false);
            if (result.data.success === true) {
                hideTemplateDialog();
                ref.current?.showToast('success', 'Success', 'Template updated successfully');
            } else {
                ref.current?.showToast('error', 'Error', 'Error updating Template');
            }
            fetchData();
        }).catch((err) => {
            ref.current?.setLoading(false);
            ref.current?.showToast('error', 'Error', 'Error updating Template');
        });
    }

    const uploadHandler = (ev) => {

        confirmPopup({
            target: ev.target,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept,
            reject
        });
    }

    const accept = (ev) => {
        const files = fileRef.current.getFiles()
        if (files && files[0]) {
            ref.current?.setLoading(true);
            const formData = new FormData();
            for (let i = 0; i < files.length; i++) {
                const file = files[i];
                formData.append('file', file);
            }
            axios.post(`${URL.BASE}api/template/upload`, formData).then((result) => {
                ref.current?.setLoading(false);
                console.log('Result = ', result);
                if (result.data.success === true) {
                    fetchData();
                    fileRef.current.clear();
                    ref.current?.showToast('success', 'Success', 'Image Uploaded Successfully');
                } else {
                    const msg = result.data.msg ? result.data.msg : 'Image Upload Failed';
                    ref.current?.showToast('error', 'Error', msg);
                }
            }).catch((e) => {
                ref.current?.setLoading(false);
                console.log(e);
            });
        }
    }

    const reject = (ev) => {
        console.log('called');
        fileRef.current.clear();
    }

    return (
        <div className='px-3 template'>

            <Common ref={ref} />{/* <> Common Components */}

            {(role == 1 || role == 2) &&
                <div className='col'>
                    <div className='pb-3 text-right'>
                        <FileUpload mode="basic" ref={fileRef} name="file[]" multiple accept="*/*" chooseLabel="Upload Image" customUpload={true} maxFileSize={1000000}
                            onSelect={uploadHandler} className="p-button-sm" />
                    </div>
                </div>
            }

            {/* Grid */}
            <div className='grid'>
                {data.map((g, i) => {
                    return <div className='col-3' key={g.id}>
                        <Card header={(e) => header(g)} footer={() => footer(g)}>
                            <div className='h-2rem'>
                                {!g.name && <div><small className='custom-tooltip' data-pr-tooltip={g.file_name}>{g.file_name}</small><br /></div>}
                                {g.name && <div><small className='custom-tooltip' data-pr-tooltip={g.name}>{g.name}</small><br /></div>}
                                <small className='custom-tooltip' data-pr-tooltip={g.desc}>{ellipse(g.desc, 40)}</small>
                            </div>
                        </Card>
                    </div>
                })}

                {data && data.length == 0 && <div>No Template Found. Go to <Link to="/admin/account">Account</Link> to sync the template</div>}
            </div>

            <Dialog header="Update Template" visible={showTemplateDialog} className='w-4' onHide={hideTemplateDialog}>
                <div className='my-auto'>
                    <form onSubmit={updateTemplate}>
                        <div className='grid'>
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="name">Template Name</label><br />
                                    <InputText id="name" name="name" placeholder="Enter Template Name" className='w-12' value={form.name} onChange={(e) => handleChange(e, form, setForm)} />
                                </div>
                            </div>
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="desc">Message</label><br />
                                    <InputTextarea name="desc" id="desc" rows={6} placeholder="Enter Description" className='w-12' value={form.desc} onChange={(e) => handleChange(e, form, setForm)} />
                                </div>
                            </div>
                        </div>

                        <div className="field text-right">
                            <Button type="button" label="Cancel" severity="secondary" icon="pi pi-times" onClick={hideTemplateDialog} /> &nbsp;
                            <Button type='submit' label="Update" severity="primary" icon="pi pi-check" />
                        </div>
                    </form>
                </div>
            </Dialog>
        </div>
    );

}

export default TemplateComponent;