
import axios from 'axios';
import { Button } from 'primereact/button';
import { Chip } from 'primereact/chip';
import { Column } from 'primereact/column';
import { confirmPopup } from 'primereact/confirmpopup';
import { DataTable } from 'primereact/datatable';
import { Dialog } from 'primereact/dialog';
import { Dropdown } from 'primereact/dropdown';
import { FileUpload } from 'primereact/fileupload';
import { InputText } from 'primereact/inputtext';
import { Paginator } from 'primereact/paginator';
import { Tooltip } from 'primereact/tooltip';

import React, { useEffect, useRef, useState } from 'react';

import Common from './Common';
import { URL, ellipse, formatDate } from './Constant';

function ContactComponent() {

    const role = localStorage.getItem('role');
    const ref = useRef(null);
    const fileRef = useRef(null);
    const searchTxtRef = useRef('');
    const [numFilter, setNumFilter] = useState(1);

    const [state, setState] = useState({ first: 0, items: [], totalRecords: 0, currentPage: 1, perPage: 10, modal: false });
    const [selectedContacts, setSelectedContacts] = useState([]);
    const [selectedGroup, setSelectedGroup] = useState(null);
    const [submitted, setSubmitted] = useState(true);
    const [importDialogVisible, setImportDialogVisible] = useState(false);
    const [addContactDialogVisible, setAddContactDialogVisible] = useState(false);
    const [showAssignGroupDialog, setShowAssignGroupDialog] = useState(false);
    const [form, setForm] = useState({ name: "", mobile: "", city: '', state: '', tags: '', active: true, whatsapp_enabled: 1 });
    const [btnName, setBtnName] = useState("Create");
    const [groups, setGroups] = useState([]);

    useEffect(() => {
        if (groups.length === 0) getGroups(); // Get all groups
        if (submitted === true) {
            setSubmitted(false);
            fetchData(); // Get all contacts
        }
    }, [submitted])

    const fetchData = (ev) => {
        if (ev) ev.preventDefault();
        setSelectedContacts([]);
        ref.current?.setLoading(true);
        const url = `${URL.BASE}api/contact?p=${state.currentPage}&page_size=${state.perPage}&search=${searchTxtRef.current.value}&filter=${numFilter}`;
        axios.get(url)
            .then((result) => {
                ref.current?.setLoading(false);
                console.log('Res = ', result.data);
                setState({ ...state, items: result.data.results, totalRecords: result.data.count });
                console.log('Data = ', state.totalRecords);

            })
            .catch((err) => {
                ref.current?.setLoading(false);
            });
    }

    const getGroups = () => {
        axios.get(`${URL.BASE}api/group`).then((result) => { setGroups(result.data.data); })
    }

    const handleChange = (e) => {
        console.log('called');
        const { name, value } = e.target;
        setForm({
            ...form,
            [name]: value
        })
    }

    const accept = () => {
        const files = fileRef.current.getFiles()
        if (files && files[0]) {
            ref.current?.setLoading(true);
            const formData = new FormData();
            formData.append('file', files[0]);
            axios.post(`${URL.BASE}api/contact/import`, formData).then((result) => {
                ref.current?.setLoading(false);
                console.log('Result = ', result);
                if (result.data.success === true) {
                    setSubmitted(true);
                    setImportDialogVisible(false);
                    fileRef.current.clear();
                    ref.current?.showToast('success', 'Success', 'File Uploaded Successfully');
                } else {
                    const msg = result.data.msg ? result.data.msg : 'File Uploaded Failed. Please validate numbers';
                    ref.current?.showToast('error', 'Error', msg);
                }
            }).catch((e) => {
                ref.current?.setLoading(false);
                console.log(e);
            });
        }
    }

    const uploadHandler = (event) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept,
            function(ev) {
                fileRef.current.clear();
            }
        });
    }

    const createContact = (ev) => {
        ev.preventDefault();
        form.user_id = 1;
        axios.post(`${URL.BASE}api/contact/create`, form).then((result) => {
            ref.current?.setLoading(false);
            if (result.data.success === true) {
                setSubmitted(true);
                setAddContactDialogVisible(false);
                setSelectedContacts([]);
                setForm({ name: "", mobile: "", city: '', state: '', tags: '', active: 0, whatsapp_enabled: 1 })
                const msg = (form.id ? 'Updated' : 'Created') + ' successfully';
                ref.current?.showToast('success', 'Success', msg);
            } else {
                const msg = result.data.msg ? result.data.msg : 'Failed to create/update contact. Please validate form.';
                ref.current?.showToast('error', 'Error', msg);
            }
            setBtnName("Create");
        });
    }

    const whatsappEnabled = (ev, whatsappEnabled) => {
        ev.preventDefault();

        confirmPopup({
            target: ev.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                const ids = selectedContacts.map(c => c.id);
                axios.post(`${URL.BASE}api/contact/whatsapp_enabled/${whatsappEnabled}`, ids).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        setSelectedContacts([]);
                        ref.current?.showToast('success', 'Success', 'Marked as verified');
                    } else {
                        const msg = result.data.msg ? result.data.msg : 'Failed to updated';
                        ref.current?.showToast('error', 'Error', msg);
                    }
                });
            }
        });
    }

    const onPageChange = (ev) => {
        setState({ ...state, currentPage: (ev.page + 1), first: ev.first, perPage: ev.rows });
        setSubmitted(true)
    }

    const deleteContact = (event, id) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                let ids = [];
                if (id) ids = [id];
                else ids = selectedContacts.map(c => c.id);

                axios.post(`${URL.BASE}api/contact/delete`, ids).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        setSelectedContacts([]);
                        ref.current?.showToast('success', 'Success', 'Contact deleted successfully.');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Deleting contact failed.');
                    }
                }).catch(() => {
                    ref.current?.showToast('error', 'Error', 'Something went wrong. Please contact administrator.');
                    ref.current?.setLoading(false);
                });
            }
        });
    }

    const editContact = (row) => {
        setBtnName("Update");
        setForm(row);
        setAddContactDialogVisible(true);
    }

    const cancelAssignGroupDialog = (clearContacts) => {
        setShowAssignGroupDialog(false);
        setSelectedGroup(null);
        if (clearContacts === true) setSelectedContacts([]);
    }

    const openAssignGroupDialog = (e) => {
        if (selectedContacts.length > 0) {
            setShowAssignGroupDialog(true);
        } else {
            ref.current?.showToast('warn', 'Alert', 'Please select contacts');
        }
    }

    const assignGroup = (ev) => {
        console.log(selectedContacts);
        confirmPopup({
            target: ev.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);

                const selectedContIds = selectedContacts.map(c => c.id);
                const o = {
                    group_id: selectedGroup.id,
                    contacts: selectedContIds,
                    user_id: localStorage.getItem('user_id')
                };

                axios.post(`${URL.BASE}api/group_contact_map/assign`, o).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        ref.current?.showToast('success', 'Success', 'Group assigned successfully');
                        cancelAssignGroupDialog(true);
                    } else {
                        const msg = result.data.msg ? result.data.msg : 'Deleting group faile';
                        ref.current?.showToast('error', 'Error', msg);
                    }
                });
            }
        });
    }

    const actionBodyTemplate = (row) => {
        return (
            <div className="inline  justify-content-center gap-3">
                {(role == 1) &&
                    <Button icon="pi pi-trash" rounded text severity="danger" size="small" onClick={(e) => { deleteContact(e, row.id) }} />
                }
                <Button icon="pi pi-user-edit" rounded text severity="secondary" size="small" onClick={(e) => { editContact(row) }} />
            </div>
        );
    }

    const activeTableTemplate = (row) => {
        if (row.active === true) return <i className="pi pi-circle-fill" style={{ color: 'green' }}></i>;
        else return <i className="pi pi-circle"></i>;
    }

    const leftContent = () => {
        let html = state.totalRecords + ' Records';
        if (selectedContacts && selectedContacts.length > 0) html += `. ( ${selectedContacts.length} Selected )`;
        return html;
    }

    const mobileTemplate = (row) => {
        const html = <div>
            {row.whatsapp_enabled == 0 && <span className='text-300'>{row.mobile}</span>}
            {row.whatsapp_enabled == 1 && <span>{row.mobile}</span>}
            {row.whatsapp_enabled == 2 && <span className='text-red-500'>{row.mobile}</span>}
        </div>
        return html;
    }

    const selecyWhatsAppFilter = (e) => {
        console.log('Value = ', e.target.value);
        setNumFilter(e.target.value);
        setSubmitted(true);
    }

    const tagsTemplate = (rowData) => {
        const tags = ellipse(rowData.tags, 20);
        const tagsList = tags.split(',');
        console.log(tagsList);


        return (
            <div>
                <Tooltip target={`.name-tooltip-${rowData.id}`} content={rowData.tags} />
                {/* <span className={`name-tooltip-${rowData.id}`}>{tag}</span> */}
                {tagsList.map((val) => (
                    <Chip label={val} className={`mr-1 name-tooltip-${rowData.id}`} />
                ))}
            </div>
        );
    }

    const groupsTemplate = (rowData) => {
        const groups = ellipse(rowData.groups, 30);
        const groupList = groups.split(',');
        return (
            <div>
                <Tooltip target={`.group-tooltip-${rowData.id}`} content={rowData.groups.substring(0, (rowData.groups.length - 1))} />
                {groupList.map((val) => (
                    val ? <Chip label={val} className={`mr-1 group-tooltip-${rowData.id}`} /> : ''

                ))}
            </div>
        );
    }

    // UI START
    return (
        <div>
            <Common ref={ref} />{/* <> Common Components */}

            <div>
                <div className='grid'>
                    <div className='col'>
                        <div className='flex pb-3 text-left'>
                            <form onSubmit={fetchData}>
                                <InputText placeholder='Enter keyword and search' ref={searchTxtRef} className='p-inputtext-sm' />&nbsp;
                                <Button type='submit' label="Search" icon="pi pi-search" size='small' onClick={fetchData} />
                            </form>
                            &nbsp;
                            <Dropdown optionLabel="name" value={numFilter} className='p-inputtext-sm' onChange={selecyWhatsAppFilter}
                                options={[{ 'name': 'Yet To Verify', 'value': 0 }, { 'name': 'WhatsApp Available', 'value': 1 }, { 'name': 'WhatsApp Not Available', 'value': 2 }]} />
                        </div>
                    </div>
                    {(role == 1 || role == 2) &&
                        <div className='col'>
                            <div className='pb-3 text-right'>
                                {(selectedContacts.length > 0 && (numFilter === 0 || numFilter === 2)) && <Button label='Available' severity='success' icon="pi pi-whatsapp" size="small" onClick={(e) => whatsappEnabled(e, 1)} />}&nbsp;
                                {(selectedContacts.length > 0 && numFilter === 1) && <Button label='Not Available' severity='danger' icon="pi pi-whatsapp" size="small" onClick={(e) => whatsappEnabled(e, 2)} />}&nbsp;
                                {selectedContacts.length > 0 && <Button label='Delete' severity='danger' icon="pi pi-trash" size="small" onClick={deleteContact} />}&nbsp;
                                <Button label="Assign Group" icon="pi pi-users" size='small' onClick={openAssignGroupDialog} />&nbsp;
                                <Button label="Add Contact" icon="pi pi-user-plus" size='small' onClick={() => setAddContactDialogVisible(true)} />&nbsp;
                                <Button icon="pi pi-file-import" size='small' severity='success' onClick={() => setImportDialogVisible(true)} />
                            </div>
                        </div>
                    }
                </div>
                {/* {(selectedContacts && selectedContacts.length > 0) &&
                    <div className='pl-3 pb-3'>
                        <span className='text-primary'>{selectedContacts.length} Selected</span>
                    </div>
                } */}

                <Paginator first={state.first} rows={state.perPage} totalRecords={state.totalRecords} rowsPerPageOptions={[10, 50, 100, 500]}
                    onPageChange={onPageChange} leftContent={leftContent} className="small-paginator" />
                <DataTable value={state.items} tableStyle={{ minWidth: '50rem' }} selectionMode="checkbox" selection={selectedContacts}
                    onSelectionChange={(e) => setSelectedContacts(e.value)} dataKey="id">

                    <Column selectionMode="multiple" headerStyle={{ width: '2%' }}></Column>
                    <Column field={(row) => formatDate(row.created_on)} header="Created On" style={{ width: '5%' }}></Column>
                    <Column field="name" header="Name" style={{ width: '20%' }}></Column>
                    <Column header="Mobile" body={mobileTemplate} style={{ width: '10%' }}></Column>
                    {/* <Column field="city" header="City" style={{ width: '10%' }}></Column> */}
                    <Column field="groups" header="Groups" body={groupsTemplate} style={{ width: '25%' }}></Column>
                    <Column field="tags" body={tagsTemplate} header="Tags" style={{ width: '25%' }}></Column>
                    <Column header="Action" body={actionBodyTemplate} style={{ width: '12%' }}></Column>
                </DataTable>

            </div>

            {/* Import file dialog */}
            <Dialog header="Import Contacts" visible={importDialogVisible} onHide={() => { if (!importDialogVisible) return; setImportDialogVisible(false); }}>
                <div className='my-auto'>
                    <FileUpload mode="basic" ref={fileRef} name="file" accept="*/*"
                        chooseLabel="Browse" customUpload={true} />
                    <small>Import 500 contacts for the fast and better performance</small>
                </div>
                <div className='mt-5 mb-2 text-right'>
                    <Button label="Cancel" severity="secondary" icon="pi pi-times" size="small" onClick={() => setImportDialogVisible(false)} /> &nbsp;
                    <Button label="Import" severity="primary" icon="pi pi-check" size="small" onClick={uploadHandler} />
                </div>
            </Dialog>

            {/* Add new contact dialog */}
            <Dialog header="Add Contact" visible={addContactDialogVisible} className='w-6' onHide={() => { if (!addContactDialogVisible) return; setAddContactDialogVisible(false); }}>
                <div className='my-auto'>
                    <form onSubmit={createContact}>
                        <div className='grid'>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="name">Name</label><br />
                                    <InputText id="name" name="name" placeholder="Enter Name" className='w-12 p-inputtext-sm' value={form.name} onChange={handleChange} />
                                </div>
                            </div>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="mobile">Mobile</label><br />
                                    <InputText name="mobile" id="mobile" placeholder="Enter Mobile" className='w-12 p-inputtext-sm' value={form.mobile} onChange={handleChange} />
                                </div>
                            </div>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="mobile">City</label><br />
                                    <InputText name="city" id="city" placeholder="Enter City" className='w-12 p-inputtext-sm' value={form.city} onChange={handleChange} />
                                </div>
                            </div>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="mobile">State</label><br />
                                    <InputText name="state" id="state" placeholder="Enter State" className='w-12 p-inputtext-sm' value={form.state} onChange={handleChange} />
                                </div>
                            </div>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="tags">Tags</label><br />
                                    <InputText name="tags" id="tags" placeholder="Enter Tags" className='w-12 p-inputtext-sm' value={form.tags} onChange={handleChange} />
                                </div>
                            </div>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="active">Active</label><br />
                                    <Dropdown value={form.active} name="active" className='w-12 p-inputtext-sm' onChange={handleChange} options={[{ 'name': 'TRUE', 'value': true }, { 'name': 'FALSE', 'value': false }]} optionLabel="name" />
                                </div>
                            </div>
                            <div className='col-6'>
                                <div className="field">
                                    <label htmlFor="active">WhatsApp Status</label><br />
                                    <Dropdown value={form.whatsapp_enabled} name="whatsapp_enabled" className='w-12 p-inputtext-sm' onChange={handleChange} options={[{ 'name': 'Need Verify', 'value': 0 }, { 'name': 'Available', 'value': 1 }, { 'name': 'Not Available', 'value': 2 }]} optionLabel="name" />
                                </div>
                            </div>
                        </div>

                        <div className="field text-right">
                            <Button type="button" label="Cancel" severity="secondary" icon="pi pi-times" size="small" onClick={() => { setAddContactDialogVisible(false); setBtnName("Create") }} /> &nbsp;
                            <Button type='submit' label={btnName} severity="primary" icon="pi pi-check" size="small" />
                        </div>
                    </form>
                </div>
            </Dialog>

            {/* Assign group dialog */}
            <Dialog header="Assign Group" visible={showAssignGroupDialog} onHide={cancelAssignGroupDialog}>
                <div className='pb-3'>
                    <span className='text-primary'>{selectedContacts.length} Selected</span>
                </div>
                <Dropdown value={selectedGroup} onChange={(e) => setSelectedGroup(e.value)} options={groups} optionLabel="name"
                    placeholder="Select a group" filter className="w-full" />
                <div className='py-3'>
                    <Button label="Cancel" icon="pi pi-times" severity='secondary' size="small" className='mr-1' onClick={cancelAssignGroupDialog} />
                    <Button label="Assign Group" icon="pi pi-check" size="small" onClick={assignGroup} />
                </div>
            </Dialog>
        </div>
    );

}

export default ContactComponent;