import axios from 'axios';
import { Button } from 'primereact/button';
import { Card } from 'primereact/card';
import { Divider } from 'primereact/divider';
import React, { useEffect, useRef, useState } from 'react';

import { useNavigate } from 'react-router-dom';
import Common from './Common';
import { URL } from './Constant';

function Home() {
    const navigate = useNavigate();

    const ref = useRef(null);
    const [data, setData] = useState({});

    useEffect(() => {
        fetchData();
    }, [])

    const fetchData = () => {
        ref.current?.setLoading(true);
        axios.get(`${URL.BASE}api/app/dashboard/` + localStorage.getItem('user_id'))
            .then((result) => {
                ref.current?.setLoading(false);
                setData(result.data.data);
            })
            .catch((err) => {
                ref.current?.setLoading(false);
            });
    }

    const redirectTo = (url) => {
        console.log('URL= ', url);
        navigate(url)
    }


    return (
        <div>
            <Common ref={ref} />{/* <> Common Components */}
            <div className='grid'>
                <div className='col-12 text-right'>
                    <Button label='Refresh' icon='pi pi-refresh' className='mr-2' size='small' severity='secondary' onClick={fetchData} />
                </div>
                <div className='col md:col-4 lg:col-3'>
                    <Card>
                        <div className='flex'>
                            <div className='flex align-items-center mr-4'>
                                <i className="pi pi-address-book text-indigo-500" style={{ fontSize: '5rem' }}></i>
                            </div>
                            <div className='flex align-items-center'>
                                <div>
                                    <h3 className="m-0 text-teal-500 font-medium pointer" onClick={() => redirectTo('/admin/contact')}>Contacts</h3>
                                    <h1 className="m-0" style={{ fontSize: '3rem' }}>
                                        {data.contacts?.contact_count}
                                    </h1>
                                </div>
                            </div>
                        </div>
                    </Card>
                </div>
                <div className='col md:col-4 lg:col-3'>
                    <Card>
                        <div className='flex'>
                            <div className='flex align-items-center mr-4'>
                                <i className="pi pi-users text-pink-500" style={{ fontSize: '5rem' }}></i>
                            </div>
                            <div className='flex align-items-center'>
                                <div>
                                    <h3 className="m-0 text-pink-500 font-medium">Groups</h3>
                                    <h1 className="m-0" style={{ fontSize: '3rem' }}>{data.group_count}</h1>
                                </div>
                            </div>
                        </div>
                    </Card>
                </div>
                <div className='col md:col-4 lg:col-3'>
                    <Card>
                        <div className='flex'>
                            <div className='flex align-items-center mr-4'>
                                <i className="pi pi-megaphone text-teal-500" style={{ fontSize: '5rem' }}></i>
                            </div>
                            <div className='flex align-items-center'>
                                <div>
                                    <h3 className="m-0 text-teal-500 font-medium">Campaigns</h3>
                                    <h1 className="m-0" style={{ fontSize: '3rem' }}>
                                        {data.campaign?.total}
                                        <span className='ml-3'>
                                            <span className='text-2xl text-green-500 mr-3'>
                                                <i className="pi pi-check-circle mr-2 font-bold"></i>{data.campaign?.success}
                                            </span>
                                            <span className='text-2xl text-red-500'>
                                                <i className="pi pi-times-circle mr-2 font-bold"></i>{data.campaign?.fail}
                                            </span>
                                        </span>
                                    </h1>
                                </div>
                            </div>
                        </div>
                    </Card>
                </div>
                <div className='col md:col-4 lg:col-3'>
                    <Card>
                        <div className='flex'>
                            <div className='flex align-items-center mr-4'>
                                <i className="pi pi-images text-orange-500" style={{ fontSize: '5rem' }}></i>
                            </div>
                            <div className='flex align-items-center'>
                                <div>
                                    <h3 className="m-0 text-orange-500 font-medium">Templates</h3>
                                    <h1 className="m-0" style={{ fontSize: '3rem' }}>{data.template_count}</h1>
                                </div>
                            </div>
                        </div>
                    </Card>
                </div>

                {/* Row 2 */}
                <div className='col md:col-4 lg:col-3'>
                    <Card>
                        <div className='flex justify-content-between'>
                            <div className='flex'>
                                <i className="pi pi-hourglass text-primary font-bold"></i> &nbsp; Yet To Verify
                            </div>
                            <div className='flex'>
                                {data.contacts?.yet_to_verify_count}
                            </div>
                        </div>
                        <Divider />
                        <div className='flex justify-content-between'>
                            <div className='flex'>
                                <i className="pi pi-whatsapp text-green-500 font-bold"></i> &nbsp; WhatsApp Available
                            </div>
                            <div className='flex'>
                                {data.contacts?.whatsapp_count}
                            </div>
                        </div>
                        <Divider />
                        <div className='flex justify-content-between'>
                            <div className='flex'>
                                <i className="pi pi-times-circle text-red-500 font-bold"></i> &nbsp; Not In WhatsApp
                            </div>
                            <div className='flex'>
                                {data.contacts?.failed_count}
                            </div>
                        </div>
                        <Divider />
                        {/* <div className='flex justify-content-between'>
                            <div className='flex'>
                                <i className="pi pi-ban text-red-500 font-bold"></i> &nbsp; Reverification Failed
                            </div>
                            <div className='flex'>
                                {data.contacts?.reverify_failed_count}
                            </div>
                        </div> */}
                    </Card>
                </div>
            </div>

            {/* <Chip label={data.contacts?.whatsapp_count} icon="pi pi-whatsapp text-green-500" className='mr-2' />
            <Chip label={data.contacts?.failed_count} icon="pi pi-whatsapp text-green-500" className='mr-2' />
            <Chip label={data.contacts?.yet_to_check_count} icon="pi pi-whatsapp text-green-500" className='mr-2' /> */}
        </div>
    )
}

export default Home;