import axios from 'axios';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import React, { useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Common from './Common';
import { URL } from './Constant';

function Login() {

    const navigate = useNavigate();
    const ref = useRef(null);

    const [form, setForm] = useState({
        mobile: "",
        password: ""
    });

    const [errors, setErrors] = useState({});

    const validateForm = () => {
        let isValid = true;
        const newErrors = {};

        if (!form.mobile) {
            newErrors.mobile = "Mobile is required";
            isValid = false;
        }

        if (!form.password) {
            newErrors.password = "Password is required";
            isValid = false;
        }
        setErrors(newErrors);
        return isValid;
    }

    const handleChange = (e) => {
        console.log('called');
        const { name, value } = e.target;
        setForm({
            ...form,
            [name]: value
        })
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        if (validateForm()) {
            console.log("Form data:", form);
            login();
        } else {
            console.log("Form Error");
        }
    }

    const login = (e) => {
        ref.current?.setLoading(true);
        axios.post(`${URL.BASE}api/user/login`, form)
            .then((result) => {
                ref.current?.setLoading(false);
                if (result.data.success === true) {
                    localStorage.setItem('user_id', 1); // result.data.data.id
                    localStorage.setItem('role', result.data.data.role);
                    localStorage.setItem('name', result.data.data.name);
                    navigate('/admin/home');
                } else {
                    const msg = result.data.msg ? result.data.msg : 'Check your credentials';
                    ref.current?.showToast('error', 'Error', msg);
                }
            })
            .catch(() => {
                ref.current?.setLoading(false);
                ref.current?.showToast('error', 'Error', 'Something went wrong. Please try again later.');
            });
    }

    return (
        <div className='grid mx-auto w-min'>

            <Common ref={ref} />{/* <> Common Components */}

            <div className='col text-center mt-5'>
                <h1 className='mb-1 text-green-600'>WhatsApp</h1>
                <h1 className='mt-0'>Promotions</h1>
            </div>
            <div className='col'>
                <form onSubmit={handleSubmit}>
                    <div className="field">
                        <label htmlFor="mobile">Mobile</label><br />
                        <InputText id="mobile" name="mobile" placeholder="Enter Mobile" value={form.mobile} onChange={handleChange} />
                        {errors.mobile && <small className="text-red-500">{errors.mobile}</small>}
                    </div>
                    <div className="field">
                        <label htmlFor="password">Password</label><br />
                        <InputText type="password" name="password" id="password" placeholder="Enter Password" value={form.password} onChange={handleChange} />
                        {errors.password && <small className="text-red-500">{errors.password}</small>}
                    </div>
                    <div className="field">
                        <Button type="submit" label="Login" className="w-full" />
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Login;