
import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { Button } from 'primereact/button';
import { Card } from 'primereact/card';
import { confirmPopup } from 'primereact/confirmpopup';
import { Dialog } from 'primereact/dialog';
import { Dropdown } from 'primereact/dropdown';
import { Image } from 'primereact/image';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';

import Common from './Common';
import { URL, ellipse } from './Constant';


function GroupComponent() {

    const navigate = useNavigate();

    const role = localStorage.getItem('role');
    const ref = useRef(null);
    const fileRef = useRef(null);
    const searchTxtRef = useRef('');
    const [groups, setGroups] = useState([]);
    const [templates, setTemplates] = useState([]);
    const [selectedGroup, setSelectedGroup] = useState(null);
    const [templatePath, setTemplatePath] = useState(null);
    const [templateVal, setTemplateVal] = useState(null);
    const [selectedImgUrl, setSelectedImgUrl] = useState(null);
    const [submitted, setSubmitted] = useState(true);
    const [showDialog, setShowDialog] = useState(false);
    const [showSendMsgDialog, setShowSendMsgDialog] = useState(false);
    const [group, setGroup] = useState({ name: '', description: '' });
    const [form, setForm] = useState({ message: '', img_path: '', campaign_name: '' });
    const [btnName, setBtnName] = useState("Create");

    useEffect(() => {
        if (submitted === true) {
            fetchTemplates();
            fetchData();
        }
    }, [submitted])

    const fetchData = (ev) => {
        if (ev) ev.preventDefault();
        ref.current?.setLoading(true);
        axios.get(`${URL.BASE}api/group?search=${searchTxtRef.current.value}`)
            .then((result) => {
                ref.current?.setLoading(false);

                setGroups(result.data.data);
                console.log(groups);
                setSubmitted(false);
            })
            .catch((err) => {
                ref.current?.setLoading(false);
            });
    }

    const fetchTemplates = () => {
        axios.get(`${URL.BASE}api/template/all_with_path`).then((result) => {
            const o = result.data.data;

            setTemplatePath(o.path);
            setTemplates(o.items);
        });
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        setForm({
            ...form,
            [name]: value
        })
    }

    const handleChangeGroup = (e) => {
        const { name, value } = e.target;
        setGroup({
            ...group,
            [name]: value
        })
    }

    const createGroup = (ev) => {
        ev.preventDefault();
        ref.current?.setLoading(true);
        group.user_id = localStorage.getItem('user_id');
        axios.post(`${URL.BASE}api/group/create`, group)
            .then((result) => {
                ref.current?.setLoading(false);
                if (result.data.success === true) {
                    setSubmitted(true);
                    setShowDialog(false);
                    setGroup({ name: "", description: "" })
                    const msg = (group.id ? 'Updated' : 'Created') + ' successfully';
                    ref.current?.showToast('success', 'Success', msg);
                } else {
                    const msg = result.data.msg ? result.data.msg : 'Failed to create/update group. Please validate form.';
                    ref.current?.showToast('error', 'Error', msg);
                }
                setBtnName("Create");
            })
            .catch(() => {
                ref.current?.setLoading(false);
            });
    }

    const deleteGroup = (event, id) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                axios.delete(`${URL.BASE}api/group/${id}`).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        ref.current?.showToast('success', 'Success', 'Group deleted successfully');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Deleting group failed');
                    }
                });
            }
        });
    }

    const editGroup = (row) => {
        setBtnName("Update");
        setGroup(row);
        setShowDialog(true);
    }

    const openSendMsgDialog = (group) => {
        setShowSendMsgDialog(true);
        setSelectedGroup(group);
    }

    const onImgSelect = (e) => {
        let files = e.files;
        if (files.length > 0) setSelectedImgUrl(files[0].objectURL);
    };

    const clearSelectedimg = (e) => {
        setSelectedImgUrl(null);
        fileRef.current.clear();
    }

    const clearnGroupForm = (e) => {
        setShowDialog(false);
        setBtnName("Create");
        setGroup({ name: '', description: '' });
    }

    const clearPromotionForm = (e) => {
        console.log('called');
        setShowSendMsgDialog(false);
        setBtnName("Create");
        setForm({ message: '', img_path: '', campaign_name: '' });
        setTemplateVal('');
    }

    const sendPromotion = (ev) => {
        ev.preventDefault();

        ref.current?.setLoading(true);
        form['group_id'] = selectedGroup.id;
        axios.post(`${URL.BASE}api/group_contact_map/start_campaign`, form)
            .then((result) => {
                ref.current?.setLoading(false);
                if (result.data.success === true) {
                    setSubmitted(true);
                    clearPromotionForm();
                    ref.current?.showToast('success', 'Success', 'Campaign started. Go to Campaigns page for status.');
                } else {
                    const msg = result.data.msg ? result.data.msg : 'Failed to start campaign';
                    ref.current?.showToast('error', 'Error', msg);
                }
            })
            .catch(() => {
                ref.current?.setLoading(false);
            });
    }

    const selectTemplate = (e) => {
        const o = e.value;
        setTemplateVal(o);
        const msg = ((o && o.desc) ? o.desc : '');
        const fName = ((o && o.file_name) ? (templatePath + o.file_name) : '');

        setForm({ message: msg, img_path: fName })
    }

    // UI START
    return (
        <div>

            <Common ref={ref} />{/* <> Common Components */}

            {/* <> Main content */}
            <div>
                <div className='grid'>
                    <div className='col'>
                        <div className='pb-3 text-left'>
                            <form onSubmit={fetchData}>
                                <InputText placeholder='Enter keyword and search' ref={searchTxtRef} className='p-inputtext-sm' />&nbsp;
                                <Button type='submit' label="Search" size='small' icon="pi pi-search" />
                            </form>
                        </div>
                    </div>
                    {(role == 1 || role == 2) &&
                        <div className='col'>
                            <div className='pb-3 text-right'>
                                <Button label="Add Group" icon="pi pi-user-plus" size='small' onClick={() => setShowDialog(true)} />&nbsp;
                            </div>
                        </div>
                    }
                </div>

                {/* Grid */}
                <div className='grid'>
                    {groups.map((g) => {
                        const footer = (
                            <div className='grid'>
                                <div className='col text-left'>
                                    <Button icon="pi pi-users" rounded text size='small' label={g.total_contacts > 0 ? g.total_contacts : '0'} onClick={() => { navigate('/admin/group-detail/' + g.id); }} />
                                </div>
                                <div className='col text-right'>
                                    {(role == 1) &&
                                        <Button icon="pi pi-trash" rounded text size='small' onClick={(e) => { deleteGroup(e, g.id) }} severity="danger" />
                                    }
                                    {(role == 1 || role == 2) &&
                                        <Button icon="pi pi-file-edit" rounded text size='small' onClick={(e) => { editGroup(g) }} />
                                    }
                                    <Button icon="pi pi-whatsapp" rounded text size='small' onClick={(e) => { openSendMsgDialog(g) }} severity="success" />
                                </div>
                            </div>
                        );

                        return (
                            <div className='md:col-3 sm:col-12' key={g.id}>
                                <Card title={g.name} footer={footer}>
                                    <p className="m-0">{ellipse(g.description, 100)} &nbsp;</p>
                                </Card>
                            </div>
                        );
                    })}
                </div>
            </div>

            {/* Add new contact dialog */}
            <Dialog header="Add Group" visible={showDialog} onHide={clearnGroupForm}>
                <div className='my-auto'>
                    <form onSubmit={createGroup}>
                        <div className="field">
                            <label htmlFor="name">Name</label><br />
                            <InputText id="name" name="name" placeholder="Enter Name" className='min-w-full p-inputtext-sm' value={group.name} onChange={handleChangeGroup} />
                        </div>
                        <div className="field">
                            <label htmlFor="description">Description</label><br />
                            <InputTextarea id="description" name="description" value={group.description} rows={5} cols={30} placeholder="Enter Description" onChange={handleChangeGroup} />
                        </div>
                        <div className="field text-right">
                            <Button type="button" label="Cancel" severity="secondary" icon="pi pi-times" size='small' onClick={clearnGroupForm} /> &nbsp;
                            <Button type='submit' label={btnName} severity="primary" icon="pi pi-check" size='small' />
                        </div>
                    </form>
                </div>
            </Dialog>

            {/* Start campaign */}
            <Dialog header={"Start Campaign to " + selectedGroup?.name} visible={showSendMsgDialog} onHide={clearPromotionForm}
                className='w-6'>
                <div className='my-auto'>
                    <form onSubmit={sendPromotion}>
                        <div className="field">
                            <label htmlFor="campaign_name">Campaign Name (Optional)</label><br />
                            <InputText id="campaign_name" name="campaign_name" className='min-w-full p-inputtext-sm' value={form.campaign_name} placeholder="Enter Campaign Name"
                                onChange={handleChange} />
                        </div>
                        <div className='grid'>
                            {/* Templates dropdown */}
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="message">Templates</label><br />
                                    <Dropdown value={templateVal} options={templates} optionLabel="name" className="w-full" placeholder="Select Template"
                                        onChange={(e) => selectTemplate(e)} showClear />
                                </div>
                            </div>
                            {/* Image path */}
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="img_path">Image Path(Optional)</label><br />
                                    <div className='flex flex-row '>
                                        <InputText id="img_path" name="img_path" className='min-w-full p-inputtext-sm' value={form.img_path} placeholder="Enter Image Path"
                                            onChange={handleChange} />
                                    </div>
                                    <Image src={selectedImgUrl} alt="Image" width="250" />
                                </div>
                            </div>
                            {/* Message */}
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="message">Message</label><br />
                                    <InputTextarea id="message" name="message" className='min-w-full' value={form.message} rows={4} cols={30} placeholder="Enter Message"
                                        onChange={handleChange} />
                                </div>
                            </div>

                        </div>
                        <div className="field text-right">
                            <Button type="button" label="Cancel" severity="secondary" icon="pi pi-times" size='small'
                                onClick={clearPromotionForm} /> &nbsp;
                            <Button type='submit' label='Start Campaign' severity="primary" icon="pi pi-whatsapp" size='small' />
                        </div>
                    </form>
                </div>
            </Dialog>

        </div >
    );

}

export default GroupComponent;