import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Route, Routes } from "react-router-dom";
import CampaignComponent from './Campaign';
import { URL } from './Constant';
import ContactComponent from './Contact';
import GroupComponent from './Group';
import GroupDetailComponent from './GroupDetail';
import Header from './Header';
import Home from './Home';
import SettingComponent from './Setting';
import TemplateComponent from './Template';
import UserComponent from './User';

function LandingPage() {
    const [threadInfo, setThreadInfo] = useState({});
    const [submitted, setSubmitted] = useState(true);

    useEffect(() => {
        if (submitted == true) {
            setSubmitted(false);
            getActiveThreadInfo();
        }
    }, [submitted])

    const getActiveThreadInfo = () => {
        axios.get(`${URL.BASE}api/app/get_active_thread`).then((result) => {
            setThreadInfo(result.data.data);
            setTimeout(() => {
                setSubmitted(true);
            }, 5000);
        }).catch(() => {
            setSubmitted(true);
        });
    }

    return (
        <div>
            <Header data={threadInfo} />
            <div className='px-3 mt-3'>
                {/* Child routers */}
                <Routes>
                    <Route path="/home" element={<Home />} />
                    <Route path="/contact" element={<ContactComponent />} />
                    <Route path="/group" element={<GroupComponent />} />
                    <Route path="/group-detail/:id" element={<GroupDetailComponent />} />
                    <Route path="/campaign" element={<CampaignComponent />} />
                    <Route path="/template" element={<TemplateComponent />} />
                    <Route path="/setting" element={<SettingComponent data={threadInfo} />} />
                    <Route path="/user" element={<UserComponent />} />
                </Routes>
            </div>
        </div>

    )
}

export default LandingPage;