
import axios from 'axios';
import { FilterMatchMode } from 'primereact/api';
import { Button } from 'primereact/button';
import { Column } from 'primereact/column';
import { confirmPopup } from 'primereact/confirmpopup';
import { DataTable } from 'primereact/datatable';
import { IconField } from 'primereact/iconfield';
import { InputIcon } from 'primereact/inputicon';
import { InputText } from 'primereact/inputtext';
import { MeterGroup } from 'primereact/metergroup';
import { Sidebar } from 'primereact/sidebar';
import { TabPanel, TabView } from 'primereact/tabview';

import { Message } from 'primereact/message';

import React, { useEffect, useRef, useState } from 'react';
import Common from './Common';
import { ellipse, formatDate, URL } from './Constant';

import './css/Campaign.css';

function CampaignComponent() {

    const ref = useRef(null);
    const [campaigns, setCampaigns] = useState([]);
    const [ongoingCampaigns, setOngoingCampaigns] = useState({});
    const [campaignDetail, setCampaignDetail] = useState({ success: [], fail: [] });
    const [activeCampaign, setActiveCampaign] = useState(null);
    const [selectedCampaigns, setSelectedCampaigns] = useState([]);
    const [submitted, setSubmitted] = useState(true);
    const [intervalCount, setIntervalCount] = useState(0);
    const [sideBarVisible, setSideBarVisible] = useState(false);

    const [form, setForm] = useState({ message: "", img_path: "" });

    const [filters, setFilters] = useState({
        global: {
            value: null, matchMode: FilterMatchMode.CONTAINS
        }
    });
    const [globalFilterValue, setGlobalFilterValue] = useState('');

    useEffect(() => {
        if (submitted === true) {
            getOnGoingCampaign();
            fetchData();
            setSubmitted(false);
        }
        configureInterval();
    }, [intervalCount])

    const fetchData = () => {
        ref.current?.setLoading(true);
        axios.get(`${URL.BASE}api/campaign/all/` + localStorage.getItem('user_id'))
            .then((result) => {
                ref.current?.setLoading(false);
                setCampaigns(result.data.data);
            })
            .catch((err) => {
                ref.current?.setLoading(false);
            });
    }

    // Get ongoing campaign
    const getOnGoingCampaign = () => {
        let userId = localStorage.getItem('user_id');
        if (userId) {
            const url = `${URL.BASE}api/campaign/ongoing_campaign/` + localStorage.getItem('user_id');
            axios.get(url).then((result) => {
                setIntervalCount(intervalCount + 1);
                setOngoingCampaigns(result.data.data);
            });
        }

    }

    const configureInterval = () => {
        setTimeout(() => {
            getOnGoingCampaign();
        }, 5000);
    }

    // Get campaign details by campaign
    const getCampaignDetail = (c) => {
        ref.current?.setLoading(true);
        setActiveCampaign(c);
        const url = `${URL.BASE}api/campaign_detail/get_by_campaign/${c.id}`;
        axios.get(url).then((result) => {
            ref.current?.setLoading(false);
            setCampaignDetail(result.data.data);
            setSideBarVisible(true);
        });
    }

    const startCampaignToFailedNums = (ev, type) => {
        confirmPopup({
            target: ev.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                let url = `${URL.BASE}api/group_contact_map/send_campaign_to_failednumbers`;
                if (type === 'remain') url = `${URL.BASE}api/group_contact_map/send_campaign_to_remaining_numbers`;
                url += `/${activeCampaign.group}/${activeCampaign.id}`;

                axios.get(url, form).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        setForm({ message: "", img_path: "" })
                        ref.current?.showToast('success', 'Success', 'Campaign will start shortly');
                    } else {
                        const msg = result.data.msg ? result.data.msg : 'Failed to start campaign';
                        ref.current?.showToast('error', 'Error', msg);
                    }
                }).catch(() => { ref.current?.setLoading(false); });
            }
        });
    }

    // Contact table header
    const renderHeader = () => {
        return (
            <div className='flex flex-row'>
                <div className='flex-auto pr-5 mr-5'>
                    {ongoingStatusTemplate()}
                </div>
                <div className='flex align-items-center justify-content-right'>
                    {selectedCampaigns.length > 0 && <Button label="Delete" severity='danger' icon="pi pi-minus" onClick={deleteCampaign} />} &nbsp;
                    <Button className='mr-2' severity='secondary' onClick={fetchData}>
                        <InputIcon className="pi pi-refresh" />
                    </Button>
                    <IconField iconPosition="left">
                        <InputIcon className="pi pi-search" />
                        <InputText value={globalFilterValue} onChange={onGlobalFilterChange} placeholder="Keyword Search" />
                    </IconField>
                </div>
            </div>
        );
    };

    const onGlobalFilterChange = (e) => {
        const value = e.target.value;
        let _filters = { ...filters };

        _filters['global'].value = value;

        setFilters(_filters);
        setGlobalFilterValue(value);
    };

    const stopCampaign = (e, id) => {
        confirmPopup({
            target: e.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                axios.get(`${URL.BASE}api/campaign/stop/${id}`).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        fetchData();
                        setSelectedCampaigns([])
                        ref.current?.showToast('success', 'Success', 'Campaign will be stopped shortly');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Failed to cancel campaign');
                    }
                }).catch((err) => {
                    ref.current?.showToast('error', 'Error', 'Failed to cancel campaign');
                });
            }
        });
    }

    const statusTemplate = (row) => {
        let html = '';
        if (row.status === 1) {
            html = <div>
                <Button label="Stop" icon="pi pi-times" severity='danger' size='small' onClick={(e) => stopCampaign(e, row.id)} />
            </div>;
        } else if (row.status === 2) {
            html = <i className="pi pi-check-circle text-green-500 font-bold"></i>;
        } else if (row.status === 3) {
            html = <i className="pi pi-times-circle text-red-500 font-bold"></i>;
        } else if (row.status === 4) {
            html = <i className="pi pi-stop-circle text-indigo-500 font-bold"></i>;
        }
        return html;
    }

    const detailStatusTemplate = (row) => {
        let html = <i className="pi pi-check-circle text-green-500 font-bold"></i>;
        if (row.sent !== true) html = <i className="pi pi-times-circle text-red-500 font-bold"></i>;
        return html;
    }

    const resultTemplate = (row) => {
        let html = <span><span className='text-green-500'>{row.total_success}</span> / {row.total_contact}</span>;
        return html;
    }

    const srNumTemplate = (row, ev) => {
        return ev.rowIndex + 1;
    }

    const showSideBar = (ev) => {
        console.log('called showSideBar = ', ev.data);
        getCampaignDetail(ev.data);
    }

    const deleteCampaign = (event) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                const ids = selectedCampaigns.map(c => c.id);
                axios.post(`${URL.BASE}api/campaign/delete`, ids).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        fetchData();
                        setSelectedCampaigns([])
                        ref.current?.showToast('success', 'Success', 'Campaign(s) deleted successfully');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Failed deleting campaign');
                    }
                });
            }
        });
    }

    const onHideSidebar = () => {
        setSideBarVisible(false);
        setActiveCampaign(null);
    }

    const paginatorLeft = campaigns.length + ' Records';

    const campaignUpdatedOnFormat = (row) => {
        return formatDate(row.updated_on)
    }

    const campaignCompletedOnFormat = (row) => {
        return formatDate(row.completed_on)
    }


    // Progress bar indicator
    const ongoingStatusTemplate = () => {
        let html = '';
        let values = [];
        if (ongoingCampaigns && ongoingCampaigns.length > 0) {
            let c = ongoingCampaigns[0];
            // const total_processed = c.success_count + c.fail_count;
            const success_percentage = Math.round((c.success_count / c.campaign.total_contact) * 100)
            const fail_percentage = Math.round((c.fail_count / c.campaign.total_contact) * 100)
            values = [
                { label: `Success (${c.success_count})`, color: '#4ec815', value: success_percentage },
                { label: `Fail (${c.fail_count})`, color: '#f04621', value: fail_percentage },
            ];
            html = <div className='grid align-items-center justify-content-center'>
                <div className='col-auto mr-3'>
                    <i className="pi pi-spin pi-spinner-dotted text-3xl"></i>
                </div>
                <div className='col'>
                    <MeterGroup values={values} />
                </div>
            </div>;
        }

        return html;
    }

    // Delete failed contact numbers
    const deleteFailedNum = (e) => {
        e.preventDefault();
        confirmPopup({
            target: e.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                axios.delete(`${URL.BASE}api/campaign/delete_failed_nums/${activeCampaign.id}`).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        fetchData();
                        ref.current?.showToast('success', 'Success', 'Contact(s) deleted successfully');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Failed deleting contact');
                    }
                });
            }
        });
    }

    // Download the failed numbers
    const downloadFailedNums = (e) => {
        e.preventDefault();
        confirmPopup({
            target: e.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                axios.get(`${URL.BASE}api/campaign/download_failed_nums/${activeCampaign.id}`).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        ref.current?.showToast('success', 'Success', 'File saved in "Downloads" folder');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Failed to download the file');
                    }
                });
            }
        });
    }

    const successAlertContent = () => {
        return <div className='align-items-center'>
            <div className='pb-1'>The campaign failed for some numbers. Do you want to start campaign for remaining numbers ? </div>
            <Button label="Start Campaign" icon="pi pi-whatsapp" size='small' onClick={(e) => startCampaignToFailedNums(e, 'remain')} />
        </div>
    }

    const failAlertContent = () => {
        return <div className='align-items-center text-left'>
            <div className='pb-1'>The campaign failed for below numbers. Please follow the below instructions to improve the performance.
                <ol>
                    <li>Check these numbers are available in WhatsApp</li>
                    <li>Delete the number from group if WhatsApp is not available</li>
                </ol>
            </div>
            <Button label="Delete" icon="pi pi-trash" size='small' severity='danger' onClick={(e) => deleteFailedNum(e)} /> &nbsp;
            <Button label="Download Excel" icon="pi pi-file-excel" size='small' severity='success' onClick={(e) => downloadFailedNums(e)} />
        </div>
    }
    // UI START
    return (
        <div>

            <Common ref={ref} />{/* <> Common Components */}

            {/* <> Main content */}
            <DataTable value={campaigns} paginator rows={10} header={renderHeader} tableStyle={{ minWidth: '50rem' }} filters={filters}
                rowsPerPageOptions={[10, 25, 50]} selectionMode="checkbox" selection={selectedCampaigns}
                onSelectionChange={(e) => setSelectedCampaigns(e.value)} globalFilterFields={['name', 'msg']} dataKey="id"
                onRowClick={showSideBar} paginatorLeft={paginatorLeft}>

                <Column selectionMode="multiple" headerStyle={{ width: '5%' }}></Column>
                <Column field="updated_on" body={campaignUpdatedOnFormat} header="Started" style={{ width: '14%' }} className='pointer'></Column>
                <Column field="completed_on" body={campaignCompletedOnFormat} header="Completed" style={{ width: '14%' }} className='pointer'></Column>
                <Column field="name" header="Name" style={{ width: '20%' }} className='pointer'></Column>
                <Column field={(row) => ellipse(row.msg, 38, true)} header="Message" className='pointer'></Column>
                <Column header="Result" body={resultTemplate} style={{ width: '5%' }}></Column>
                <Column field="status" body={statusTemplate} header="Status" style={{ width: '10%' }}></Column>
                {/* <Column header="group" body={activeTableTemplate} style={{ width: '5%' }}></Column> */}
            </DataTable>

            {/* Sidebar */}
            <Sidebar header='Campaign Detail' visible={sideBarVisible} onHide={onHideSidebar} position="right" className="lg:w-50rem">

                <table className=''>
                    <tr>
                        <th className='text-left py-2 w-11rem text-500'><i className='pi pi-megaphone'></i> CAMPAIGN NAME </th>
                        <td>{activeCampaign?.name}</td>
                    </tr>
                    <tr>
                        <th className='text-left py-2 w-11rem text-500'><i className='pi pi-image'></i> IMAGE </th>
                        <td>{activeCampaign?.img_path ? activeCampaign?.img_path : 'No Image'}</td>
                    </tr>
                    <tr>
                        <th className='text-left py-2 w-11rem vertical-align-top text-500'><i className='pi pi-align-left'></i> MESSAGE </th>
                        <td>{ellipse(activeCampaign?.msg, 2000, true)}</td>
                    </tr>
                    {activeCampaign?.err_msg && <tr >
                        <th className='text-left py-2 w-11rem vertical-align-top text-500 text-red-500'><i className='pi pi-exclamation-circle'></i> ERROR </th>
                        <td className='text-red-500'>{activeCampaign?.err_msg}</td>
                    </tr>}
                </table>

                <TabView>
                    <TabPanel header="Success">
                        {((campaignDetail.fail.length + campaignDetail.success.length) !== activeCampaign?.total_contact) &&
                            <Message severity="info" content={successAlertContent} />}
                        <DataTable value={campaignDetail.success}>
                            <Column header="Sr #" body={srNumTemplate} className='w-5rem'></Column>
                            <Column header="Sent" body={campaignUpdatedOnFormat} className='w-14rem'></Column>
                            <Column header="Name" field="name"></Column>
                            <Column header="Mobile" field="mobile" className='w-10rem'></Column>
                            <Column header="Status" body={detailStatusTemplate} className='w-5rem'></Column>
                        </DataTable>
                    </TabPanel>
                    <TabPanel header="Fail">
                        {campaignDetail.fail.length > 0 && <div className='w-full'><Message severity="info" content={failAlertContent} /></div>}

                        <DataTable value={campaignDetail.fail}>
                            <Column header="Sr #" body={srNumTemplate} className='w-5rem'></Column>
                            <Column header="Sent" body={campaignUpdatedOnFormat} className='w-14rem'></Column>
                            <Column header="Name" field="name"></Column>
                            <Column header="Mobile" field="mobile" className='w-10rem'></Column>
                            <Column header="Status" body={detailStatusTemplate} className='w-5rem'></Column>
                        </DataTable>
                    </TabPanel>
                </TabView>
            </Sidebar>
        </div>
    );

}

export default CampaignComponent;