
import axios from 'axios';
import { FilterMatchMode } from 'primereact/api';
import { Button } from 'primereact/button';
import { Column } from 'primereact/column';
import { confirmPopup } from 'primereact/confirmpopup';
import { Dialog } from 'primereact/dialog';
import { Dropdown } from 'primereact/dropdown';
import { IconField } from 'primereact/iconfield';
import { Image } from 'primereact/image';
import { InputIcon } from 'primereact/inputicon';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';
import React, { useEffect, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

import { DataTable } from 'primereact/datatable';

import Common from './Common';
import { URL } from './Constant';


function GroupDetailComponent() {

    const navigate = useNavigate();

    const role = localStorage.getItem('role');
    const ref = useRef(null);
    const [selectedContacts, setSelectedContacts] = useState([]);
    const [data, setData] = useState({ group: {}, contacts: [] });
    const [submitted, setSubmitted] = useState(true);
    const [templates, setTemplates] = useState([]);
    const [templateVal, setTemplateVal] = useState(null);
    const [templatePath, setTemplatePath] = useState(null);
    const [selectedImgUrl, setSelectedImgUrl] = useState('');
    const [showSendMsgDialog, setShowSendMsgDialog] = useState(false);
    const [form, setForm] = useState({ message: '', img_path: '', campaign_name: '' });
    const [filters, setFilters] = useState({
        global: {
            value: null, matchMode: FilterMatchMode.CONTAINS
        }
    });
    const [globalFilterValue, setGlobalFilterValue] = useState('');

    const { id } = useParams();

    useEffect(() => {
        console.log('Id = ', id);
        if (submitted === true) {
            fetchTemplates();
            fetchData();
        }
    }, [submitted])

    const fetchData = () => {
        ref.current?.setLoading(true);
        axios.get(`${URL.BASE}api/group_contact_map/get_by_group/${id}`)
            .then((result) => {
                ref.current?.setLoading(false);
                setData(result.data.data);
                setSelectedContacts([]);
                setSubmitted(false);
            })
            .catch((err) => {
                ref.current?.setLoading(false);
            });
    }

    const fetchTemplates = () => {
        axios.get(`${URL.BASE}api/template/all_with_path`).then((result) => {
            const o = result.data.data;

            setTemplatePath(o.path);
            setTemplates(o.items);
        });
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        setForm({
            ...form,
            [name]: value
        })
    }

    const deleteGroup = (event, id) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                axios.delete(`${URL.BASE}api/group/${id}`).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        ref.current?.showToast('success', 'Success', 'Group deleted successfully');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Deleting group failed');
                    }
                });
            }
        });
    }

    const removeContactFromGroup = (event, id) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                let contactIds = selectedContacts.map(c => c.id);
                if (id) contactIds = [id];

                const o = { group_id: data.group.id, contact_ids: contactIds };

                ref.current?.setLoading(true);
                axios.post(`${URL.BASE}api/group_contact_map/rm_cnt_frm_gr`, o).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        setSelectedContacts([]);
                        ref.current?.showToast('success', 'Success', 'Contact(s) removed successfully');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Failed to remove contact(s)');
                    }
                });
            }
        });
    }

    const clearPromotionForm = (e) => {
        setShowSendMsgDialog(false);
        setForm({ message: '', img_path: '', campaign_name: '' });
    }

    const sendPromotion = (ev) => {
        ev.preventDefault();

        ref.current?.setLoading(true);
        form['group_id'] = data.group.id;
        axios.post(`${URL.BASE}api/group_contact_map/start_campaign`, form)
            .then((result) => {
                ref.current?.setLoading(false);
                if (result.data.success === true) {
                    setSubmitted(true);
                    setShowSendMsgDialog(false);
                    setForm({ message: '', img_path: '', campaign_name: '' })
                    ref.current?.showToast('success', 'Success', 'Campaign will start shortly');
                } else {
                    const msg = result.data.msg ? result.data.msg : 'Failed to send promotional message';
                    ref.current?.showToast('error', 'Error', msg);
                }
            })
            .catch(() => {
                ref.current?.setLoading(false);
            });
    }

    const actionBodyTemplate = (row) => {
        return (
            <div className="inline  justify-content-center gap-3">
                <Button icon="pi pi-trash" rounded text severity="danger" onClick={(e) => { removeContactFromGroup(e, row.id) }} />
            </div>
        );
    }

    const activeTableTemplate = (row) => {
        if (row.active === true) return <i className="pi pi-circle-fill" style={{ color: 'green' }}></i>;
        else return <i className="pi pi-circle"></i>;
    }

    const onGlobalFilterChange = (e) => {
        const value = e.target.value;
        console.log(value);
        let _filters = { ...filters };

        _filters['global'].value = value;

        setFilters(_filters);
        setGlobalFilterValue(value);
    };

    const selectTemplate = (e) => {
        const o = e.value;
        setTemplateVal(o);
        const msg = ((o && o.desc) ? o.desc : '');
        const fName = ((o && o.file_name) ? (templatePath + o.file_name) : '');

        setForm({ message: msg, img_path: fName })
    }

    const renderHeader = () => {
        return (
            <div>
                <div className='flex flex-row'>
                    <div className="flex-1">
                        <div className='grid align-items-center'>
                            <div className='col-auto'>
                                <Button icon="pi pi-arrow-left" text onClick={(e) => { navigate(-1) }} />&nbsp;
                            </div>
                            <div className='col'>
                                <h2 className='my-2'>{data.group.name}</h2>
                                <small>{data.group.description}</small>
                            </div>
                        </div>
                    </div>
                    <div className="flex-1">
                        <div className='flex justify-content-end'>
                            {(role == 1 || role == 2) &&
                                <Button label="Remove From Group" icon="pi pi-minus" severity="danger" onClick={removeContactFromGroup} />
                            }
                            &nbsp;<Button label="Start Promotion" icon="pi pi-whatsapp" onClick={(e) => { setShowSendMsgDialog(true); }} />&nbsp;
                            <IconField iconPosition="left">
                                <InputIcon className="pi pi-search" />
                                <InputText value={globalFilterValue} onChange={onGlobalFilterChange} placeholder="Keyword Search" />
                            </IconField>
                        </div>
                    </div>
                </div>
                {(selectedContacts && selectedContacts.length > 0) && <small>{selectedContacts.length} Selected</small>}
            </div>


        );
    };

    const header = renderHeader();

    // UI START
    return (
        <div>

            <Common ref={ref} />{/* <> Common Components */}

            {/* <> Main content */}
            <div>

                {/* Grid */}
                <div className='grid'>
                    <div className='col'>
                        <DataTable value={data.contacts} paginator rows={5} header={header} tableStyle={{ minWidth: '50rem' }} filters={filters}
                            rowsPerPageOptions={[5, 10, 25, 50]} selectionMode="checkbox" selection={selectedContacts}
                            globalFilterFields={['name', 'mobile']} onSelectionChange={(e) => setSelectedContacts(e.value)} dataKey="id"
                            currentPageReportTemplate="{first} to {last} of {totalRecords}">

                            <Column selectionMode="multiple" headerStyle={{ width: '2%' }}></Column>
                            <Column field="name" header="Name" style={{ width: '25%' }}></Column>
                            <Column field="mobile" header="Mobile" style={{ width: '10%' }}></Column>
                            <Column field="city" header="City" style={{ width: '10%' }}></Column>
                            <Column field="state" header="State" style={{ width: '10%' }}></Column>
                            <Column field="tags" header="Tags" style={{ width: '25%' }}></Column>
                            <Column header="Active" body={activeTableTemplate} style={{ width: '5%' }}></Column>
                            <Column header="Action" body={actionBodyTemplate} style={{ width: '12%' }}></Column>
                        </DataTable>
                    </div>
                </div>
            </div>

            {/* Send promotion */}
            <Dialog header="Start Campaign" visible={showSendMsgDialog} onHide={clearPromotionForm} className='w-6'>
                <div className='my-auto'>
                    <form onSubmit={sendPromotion}>
                        <div className="field">
                            <label htmlFor="campaign_name">Campaign Name</label><br />
                            <InputText id="campaign_name" name="campaign_name" className='min-w-full' value={form.campaign_name} placeholder="Enter Campaign Name"
                                onChange={handleChange} />
                        </div>
                        <div className='grid'>
                            {/* Templates dropdown */}
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="message">Templates</label><br />
                                    <Dropdown value={templateVal} options={templates} optionLabel="name" className="w-full" placeholder="Select Template"
                                        onChange={(e) => selectTemplate(e)} showClear />
                                </div>
                            </div>
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="img_path">Image Path</label><br />
                                    <div className='flex flex-row '>
                                        <InputText id="img_path" name="img_path" className='min-w-full' value={form.img_path} placeholder="Enter Image Path"
                                            onChange={handleChange} />
                                    </div>
                                    <Image src={selectedImgUrl} alt="Image" width="250" />
                                </div>
                            </div>
                            {/* Message */}
                            <div className='col-12'>
                                <div className="field">
                                    <label htmlFor="message">Message</label><br />
                                    <InputTextarea id="message" name="message" className='min-w-full' value={form.message} rows={4} cols={30} placeholder="Enter Message"
                                        onChange={handleChange} />
                                </div>
                            </div>

                        </div>
                        <div className="field text-right">
                            <Button type="button" label="Cancel" severity="secondary" icon="pi pi-times" onClick={clearPromotionForm} /> &nbsp;
                            <Button type='submit' label='Start Campaign' severity="primary" icon="pi pi-whatsapp" />
                        </div>
                    </form>
                </div>
            </Dialog>
        </div>
    );

}

export default GroupDetailComponent;