

export const formatDate = (strDate) => {
    let date = new Date(strDate);

    let year = date.getFullYear();
    let month = date.getMonth() + 1;
    month = month < 10 ? ('0' + month) : month;

    let dte = date.getDate();
    dte = dte < 10 ? ('0' + dte) : dte;

    let hours = date.getHours();
    hours = hours < 10 ? ('0' + hours) : hours;

    let minutes = date.getMinutes();
    minutes = minutes < 10 ? ('0' + minutes) : minutes;

    let dateMDYHM = `${year}/${month}/${dte} ${hours}:${minutes}`;
    return dateMDYHM;
};

export const ellipse = (txt, len, decode) => {
    if (txt && decode === true) {
        try { txt = decodeURIComponent(txt); } catch (error) { }
    }
    if (txt && txt.length > len) txt = txt.substring(0, len) + '..'
    return txt;
}
export const URL = {
    BASE: 'http://localhost/'
}

export const handleChange = (e, form, setForm) => {
    console.log('called');
    const { name, value } = e.target;
    setForm({
        ...form,
        [name]: value
    })
}
