
import axios from 'axios';
import { Button } from 'primereact/button';
import { Column } from 'primereact/column';
import { confirmPopup } from 'primereact/confirmpopup';
import { DataTable } from 'primereact/datatable';
import { Dialog } from 'primereact/dialog';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import { Password } from 'primereact/password';

import React, { useEffect, useRef, useState } from 'react';

import Common from './Common';
import { URL } from './Constant';

function UserComponent() {

    const role = localStorage.getItem('role');
    const ref = useRef(null);

    const [errors, setErrors] = useState({});
    const [state, setState] = useState({ items: [], modal: false });
    const [submitted, setSubmitted] = useState(true);
    const [addContactDialogVisible, setAddContactDialogVisible] = useState(false);
    const [form, setForm] = useState({ name: '', mobile: '', email: '', role: 2, password: '' });
    const [btnName, setBtnName] = useState("Create");

    useEffect(() => {
        if (submitted === true) {
            setSubmitted(false);
            fetchData(); // Get all contacts
        }
    }, [submitted])

    const fetchData = (ev) => {
        if (ev) ev.preventDefault();
        ref.current?.setLoading(true);
        const url = `${URL.BASE}api/user/all`;
        axios.get(url)
            .then((result) => {
                ref.current?.setLoading(false);
                setState({ ...state, items: result.data.data });
            })
            .catch((err) => {
                ref.current?.setLoading(false);
            });
    }

    const validateForm = () => {
        let isValid = true;
        const newErrors = {};
        if (!form.name) {
            newErrors.name = "Name is required";
            isValid = false;
        }

        if (!form.mobile) {
            newErrors.mobile = "Mobile is required";
            isValid = false;
        }

        if (!form.email) {
            newErrors.email = "Email is required";
            isValid = false;
        }

        if (!form.password) {
            newErrors.password = "Password is required";
            isValid = false;
        }
        setErrors(newErrors);
        return isValid;
    }

    const handleChange = (e) => {
        console.log('called');
        const { name, value } = e.target;
        setForm({
            ...form,
            [name]: value
        })
    }

    const createContact = (ev) => {
        ev.preventDefault();
        if (validateForm()) {
            form.user_id = 1;
            axios.post(`${URL.BASE}api/user/create`, form).then((result) => {
                ref.current?.setLoading(false);
                if (result.data.success === true) {
                    setSubmitted(true);
                    setAddContactDialogVisible(false);
                    setForm({ name: "", mobile: "", city: '', state: '', tags: '', active: 0, whatsapp_enabled: 1 })
                    const msg = (form.id ? 'Updated' : 'Created') + ' successfully';
                    ref.current?.showToast('success', 'Success', msg);
                } else {
                    const msg = result.data.msg ? result.data.msg : 'Failed to create/update contact. Please validate form.';
                    ref.current?.showToast('error', 'Error', msg);
                }
                setBtnName("Create");
            });
        }
    }

    const deleteContact = (event, id) => {
        confirmPopup({
            target: event.currentTarget,
            message: 'Are you sure you want to proceed?',
            icon: 'pi pi-exclamation-triangle',
            defaultFocus: 'accept',
            accept: () => {
                ref.current?.setLoading(true);
                axios.post(`${URL.BASE}api/user/delete`, [id]).then((result) => {
                    ref.current?.setLoading(false);
                    if (result.data.success === true) {
                        setSubmitted(true);
                        ref.current?.showToast('success', 'Success', 'Contact deleted successfully.');
                    } else {
                        ref.current?.showToast('error', 'Error', 'Deleting contact failed.');
                    }
                }).catch(() => {
                    ref.current?.showToast('error', 'Error', 'Something went wrong. Please contact administrator.');
                    ref.current?.setLoading(false);
                });
            }
        });
    }

    const editContact = (row) => {
        setBtnName("Update");
        // setForm({
        //     ...form,
        //     ['password']: '********************'
        // })
        row.password = '********************';
        setForm(row);
        setAddContactDialogVisible(true);
    }

    const actionBodyTemplate = (row) => {
        return (
            <div>
                {(role == 1) &&
                    <div className="inline  justify-content-center gap-3">
                        <Button icon="pi pi-trash" rounded text severity="danger" size="small" onClick={(e) => { deleteContact(e, row.id) }} />
                        <Button icon="pi pi-user-edit" rounded text severity="secondary" size="small" onClick={(e) => { editContact(row) }} />
                    </div>
                }
            </div>
        );
    }

    const srNumTemplate = (row, meta) => {
        return (meta.rowIndex + 1);
    }

    const roleTemplate = (row, meta) => {
        return (
            <span>
                {row.role == 1 && <span className='text-red-600'>Admin</span>}
                {row.role == 2 && <span className='text-green-600'>Manager</span>}
                {row.role == 3 && <span className='text-primary-600'>Employee</span>}
            </span>
        );
    }

    // UI START
    return (
        <div>
            <Common ref={ref} />{/* <> Common Components */}

            <div>
                {(role == 1) &&
                    <div className='grid'>
                        <div className='col'>
                            <div className='pb-3 text-right'>
                                <Button label="Add User" icon="pi pi-user-plus" size='small' onClick={() => setAddContactDialogVisible(true)} />&nbsp;
                            </div>
                        </div>
                    </div>
                }
                <DataTable value={state.items} tableStyle={{ minWidth: '50rem' }} selectionMode="checkbox" dataKey="id">
                    <Column header="#" body={srNumTemplate}></Column>
                    <Column field="name" header="Name" style={{ width: '5%' }}></Column>
                    <Column field="mobile" header="Mobile" style={{ width: '30%' }}></Column>
                    <Column field="email" header="Email" style={{ width: '30%' }}></Column>
                    <Column header="Role" body={roleTemplate}></Column>
                    <Column header="Action" body={actionBodyTemplate} className='' style={{ width: '12%' }}></Column>
                </DataTable>

            </div>

            {/* Add new contact dialog */}
            <Dialog header="Add User" visible={addContactDialogVisible} className='w-3' onHide={() => { if (!addContactDialogVisible) return; setAddContactDialogVisible(false); }}>
                <form onSubmit={createContact}>
                    <div className='grid'>
                        <div className='col-12'>
                            <div className="field mb-0">
                                <label htmlFor="role">Role</label><br />
                                <Dropdown optionLabel="name" value={form.role} className='w-12 p-inputtext-sm' onChange={(e) => { setForm({ ...form, ['role']: e.target.value }) }}
                                    options={[{ 'name': 'Admin', 'value': 1 }, { 'name': 'Manager', 'value': 2 }, { 'name': 'Employee', 'value': 3 }]} />
                            </div>
                        </div>
                        <div className='col-12'>
                            <div className="field mb-0">
                                <label htmlFor="name">Name</label><br />
                                <InputText id="name" name="name" placeholder="Enter Name" className='w-12 p-inputtext-sm' value={form.name} onChange={handleChange} />
                                {errors.name && <small className="text-red-500">{errors.name}</small>}
                            </div>
                        </div>
                        <div className='col-12'>
                            <div className="field mb-0">
                                <label htmlFor="email">Email</label><br />
                                <InputText id="email" name="email" placeholder="Enter Email" className='w-12 p-inputtext-sm' value={form.email} onChange={handleChange} />
                                {errors.email && <small className="text-red-500">{errors.email}</small>}
                            </div>
                        </div>
                        <div className='col-12'>
                            <div className="field mb-0">
                                <label htmlFor="mobile">Mobile</label><br />
                                <InputText id="mobile" name="mobile" placeholder="Enter Mobile" className='w-12 p-inputtext-sm' value={form.mobile} onChange={handleChange} />
                                {errors.mobile && <small className="text-red-500">{errors.mobile}</small>}
                            </div>
                        </div>
                        <div className='col-12'>
                            <div className="field">
                                <label htmlFor="password">Password</label><br />
                                <Password id="password" name="password" placeholder="Enter Password" className='w-12 p-inputtext-sm' value={form.password} onChange={handleChange} />
                                {errors.password && <small className="text-red-500">{errors.password}</small>}
                            </div>
                        </div>
                    </div>

                    <div className="field text-right">
                        <Button type="button" label="Cancel" severity="secondary" icon="pi pi-times" size="small" onClick={() => { setAddContactDialogVisible(false); setBtnName("Create"); setErrors({}); }} /> &nbsp;
                        <Button type='submit' label={btnName} severity="primary" icon="pi pi-check" size="small" />
                    </div>
                </form>
            </Dialog>

        </div>
    );

}

export default UserComponent;