import { Avatar } from 'primereact/avatar';
import { Chip } from 'primereact/chip';
import { Menubar } from 'primereact/menubar';
import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";

function Header(props) {
    const navigate = useNavigate();
    const loggedInName = localStorage.getItem('name');
    const start = (
        <div className="me-2">Wa.Automation</div>
    );

    const chipLabel = () => {
        let name = '';
        if (props.data && props.data.active_thread_name === 'WHATSAPP_CHECK') name += ' Verifying';
        else name += ' Campaign';
       
        const html = <div>
            <i className='pi pi-spin pi-spinner-dotted'></i> &nbsp;
            <span class="text-xs">{name}</span>
        </div>
        return html;
    }

    const end = (
        <div className="flex align-items-center gap-2 pointer" onClick={() => { navigate("/admin/user"); }}>
            {props.data?.driver_in_use === true && <Chip template={chipLabel} className='surface-700 text-white p-2' />}
            <small>Welcome <br/>{loggedInName}</small>
            <Avatar image="https://static.vecteezy.com/system/resources/previews/019/896/008/original/male-user-avatar-icon-in-flat-design-style-person-signs-illustration-png.png" shape="circle" />
        </div>
    );

    const items = [
        {
            label: 'Dashboard',
            icon: 'pi pi-objects-column',
            command: (e) => {
                navigate("/admin/home");
            }
        },
        {
            label: 'Contacts',
            icon: 'pi pi-address-book',
            command: (e) => {
                navigate("/admin/contact");
            }
        },
        {
            label: 'Groups',
            icon: 'pi pi-users',
            command: (e) => {
                navigate("/admin/group");
            }
        },
        {
            label: 'Campaigns',
            icon: 'pi pi-megaphone',
            command: (e) => {
                navigate("/admin/campaign");
            }
        },
        {
            label: 'Template',
            icon: 'pi pi-images',
            command: (e) => {
                navigate("/admin/template");
            }
        },
        {
            label: 'Settings',
            icon: 'pi pi-cog',
            command: (e) => {
                navigate("/admin/setting");
            }
        },
    ];

    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);

    return (
        <Menubar model={items} start={start} end={end} className='border-radius-0' />
    )
}

export default Header;